import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./ContactPageContent.module.scss"

export default () => {
  const {
    pine,
    iconPhone,
    iconMail,
    iconSkype,
    catFace,
  } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconPhone: file(relativePath: { eq: "iconPhone.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconMail: file(relativePath: { eq: "iconMail.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconSkype: file(relativePath: { eq: "iconSkype.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      catFace: file(relativePath: { eq: "catFace.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={styles.mainContent}>
      <div className={styles.headingSection}>
        <Img
          fixed={pine.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
        />
        <div className={styles.headingContainer}>
          <div>Let's Make</div>
          <div>Something Great</div>
          <div>Together</div>
        </div>
        <Img
          fixed={pine.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
          imgStyle={{
            WebkitTransform: "scaleX(-1)",
            transform: "scaleX(-1)",
          }}
        />
      </div>
      <div className={styles.contactInfoSection}>
        <div className={styles.contactInfo}>
          <Img
            fixed={iconPhone.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.contactInfoText}>(+84)962.61.81.22</div>
        </div>
        <div className={styles.contactInfo}>
          <Img
            fixed={iconSkype.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.contactInfoText}>hanguyen459</div>
        </div>
        <div className={styles.contactInfo}>
          <Img
            fixed={iconMail.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.contactInfoText}>ntbichha95@gmail.com</div>
        </div>
      </div>
      <div className={styles.noticeSection}>
        <div className={styles.noticeText}>
          Please feel free. I don't scratch.
        </div>
        <Img
          fixed={catFace.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
        />
      </div>
    </div>
  )
}
